@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);

*{
margin:0px;
padding: 0px;
font-family: 'Poppins', sans-serif;
}
.navbar{

}
.navbar__nav{
display: flex;
justify-content: space-around;
align-items: center;
height:60px;
border-bottom: 1px solid gray;

}
.nav__title{
   
font-size: 23px;
cursor: pointer;
/* padding:5px; */
padding:0px 20px 0px 20px;
}
.nav__title:hover{
    border: 1px dotted black;
    
}
.nav__unlist{
display: flex;
list-style: none;
/* border: 1px solid black; */
font-size: 18px;
padding:2px;


}
.nav__unlist li{
    
    cursor: pointer;
   margin-right:30px;
    padding:10px;
}
.nav__unlist li:hover{
    border: 1px dotted black;
}
.nav__unlist__home{

}
.nav__unlist__login{

}
.nav__unlist__singup{

}
.nav__unlist__logout{

}

@media(max-width :626px){
    .nav__title{
        font-size: 20px;
        padding:0px ;
        margin-left: 5px;
    }
}
.body{
    display: flex;
    justify-content: space-evenly;
}
.body__first{
    border: 1px solid black;
    width: 300px;
    height: 200px;
    margin-top: 40px;
    text-align:center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
.body__first h1{
    font-weight: 300;
}
.body__second{
    border: 1px solid black;
    width: 300px;
    height: 200px;
    margin-top: 40px;
    text-align:center;
    display: flex;
    flex-direction: column;

    justify-content: space-between;
}
.body__second h1{
    font-weight: 300;
}

.body__start{
    text-decoration: none;
    color:black;
    border: 1px solid black;
    padding: 10px;
    margin: 5px;
}
.body__start:hover{

}
.body__start:active{
    color:black;
}

@media(max-width :626px){
    .body{
        flex-direction: column;
        align-items: center;
    }
}
